<template>
  <div class="box">
    <el-container style="height: 100%;">
      <el-aside width="20%" >
        <el-scrollbar style='height: 100%;'  v-loading="loading">
          <metadataTree ref="moduleTree" :treeData="treeData" @node-click="orgTreeClick" style="margin-top:10px;"></metadataTree>
        </el-scrollbar>
      </el-aside>
      <el-main>
        <div class='top-btn'>
          <el-button type="primary" size="small" @click="moveDirection('up')">上移</el-button>
          <el-button type="primary" size="small" @click="moveDirection('down')">下移</el-button>
        </div>
        <el-table
          :data="tableData"
          @selection-change="selectionChangeHandle"
        >
          <el-table-column type="selection" width="50" align="center"></el-table-column>
          <el-table-column prop="expressionPrefix" label="组合">
            <template slot-scope="scope">
              <el-select v-model="scope.row.expressionPrefix" placeholder="请选择" clearable>
                <el-option
                  v-for="item in expressionPrefix"
                  :key="item.value"
                  :label="item.label"
                  :value="item.label"
                >
                </el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="名称">
            <template slot-scope="scope">
              <el-input v-model="scope.row.name" placeholder="请输入"></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="operatorShow" label="操作符">
            <template slot-scope="scope">
              <el-select v-model="scope.row.operatorShow" placeholder="请选择" clearable>
                <el-option
                  v-for="item in operatorShow"
                  :key="item.value"
                  :label="item.label"
                  :value="item.label"
                >
                </el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="valueShow" label="值">
            <template slot-scope="scope">
              <el-select v-model="scope.row.valueShow" placeholder="请选择" clearable>
                <el-option
                  v-for="item in valueShow"
                  :key="item.value"
                  :label="item.label"
                  :value="item.label"
                >
                </el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="expressionSuffix" label="组合">
            <template slot-scope="scope">
              <el-select v-model="scope.row.expressionSuffix" placeholder="请选择" clearable>
                <el-option
                  v-for="item in expressionSuffix"
                  :key="item.value"
                  :label="item.label"
                  :value="item.label"
                >
                </el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column prop="connectorShow" label="逻辑">
            <template slot-scope="scope">
              <el-select v-model="scope.row.connectorShow" placeholder="请选择" clearable>
                <el-option
                  v-for="item in connectorShow"
                  :key="item.value"
                  :label="item.label"
                  :value="item.label"
                >
                </el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column label="操作"  width="100">
            <template slot-scope="scope">
              <el-button type="danger" size="mini" plain @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div>
          <el-form ref="addForm" :model="dataForm" :rules="rules" label-width="150px">
            <el-row>
              <el-col :span="22">
                <el-form-item label="区域状态" prop="menuStatus">
                  <el-radio-group v-model="dataForm.menuStatus">
                    <el-radio label="1">启用</el-radio>
                    <el-radio label="0">停用</el-radio>
                  </el-radio-group>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="22">
                <el-form-item label="名册说明" prop="sqlConditionsCh">
                  <el-input type="textarea" :rows="2" v-model="dataForm.sqlConditionsCh"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <div class="dialog-footer">
              <el-button type="primary" size="small" @click="saveClick()">保存</el-button>
              <el-button type="primary" size="small" @click="describeClick()">生成中文描述</el-button>
            </div>
          </el-form>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
  import {swapItems} from '@/utils';
  import metadataTree from "@/components/baseComponents/orgTree.vue";
  export default{
    props: ['checkedData'],
    components: {
      metadataTree,
    },
    data(){
      return{
        loading:true,
        expressionPrefix:this.API.constObj.expressionPrefix,
        expressionSuffix:this.API.constObj.expressionSuffix,
        operatorShow:this.API.constObj.operatorShow,
        connectorShow:[
          {
            value: '1',
            label: '并且'
          },
          {
            value: '2',
            label: '或者'
          },
        ],
        valueShow:[
          {
            value: '1',
            label: '是'
          },
          {
            value: '2',
            label: '否'
          },
        ],
        treeData:[],
        tableData: [],
        currentRow:'',
        dataForm:{
          menuStatus:'1',
          sqlConditionsCh:'',
        },
        citiaoId:'',//编辑、保存需传id
        operateType:'',
        rules: {
          menuStatus: [
            { required: true, message: '请输入区域状态', trigger: 'blur' },
          ],
        }
      }
    },
    created(){
      this.getDataTree();
    },
    methods:{
      initData(operateType){
        //初始化
        this.operateType = operateType
      },
      //获取树
      getDataTree(){
        this.$http({
          url: this.$http.adornUrl(this.API.getEntryInfoTreeInformation),
          method: 'post',
          data: this.$http.adornData({})
        }).then(({data}) => {

          if(data && data.code===0){
            this.treeData=data.data;
            this.loading = false
            // this.orgTreeClick(this.treeData[0])
          }else{
            this.$message.error(data.msg);
            this.treeData=[];
          }
        });
      },
      orgTreeClick(data){
        this.currentRow = data;
        this.isParent = data.isParent;
        if(this.isParent === false){
          var list = {
            name: data.name,
            connectorShow:'并且',
            operatorShow:'等于',
          };
          this.tableData.push(list);
        }
      },
      getDataList(row){
        this.citiaoId = row.id;
        this.$http({
          url: this.$http.adornUrl(this.API.queryPersonLeadercondition),
          method: 'post',
          data: this.$http.adornData({
            id:row.id
          })
        }).then(({data}) => {
          if(data && data.code===0){
            this.tableData = JSON.parse(data.data.leaderScript);
            this.dataForm.menuStatus = data.data.leaderStatus;
            this.dataForm.sqlConditionsCh = data.data.leaderDesc;
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      //删除新增的某行数据
      handleDelete(index, row) {
        this.tableData.splice(index, 1);
      },
      selectionChangeHandle(val) {
        //多选
        this.tableDataSelections = val;
      },
      moveDirection(direction) {
        if(this.tableDataSelections.length === 0) {
          this.$confirm('请选择一条数据！', '提示', {
            confirmButtonText: '确定',
            showCancelButton: false,
            type: 'warning',
            center: true
          });
          return;
        };
        //移动
        var that = this,
          index;
        that.tableDataSelections.sort((prev, next) => {
          return that.tableData.indexOf(prev) - that.tableData.indexOf(next);
        });
        if (direction == "down") that.tableDataSelections.reverse();
        that.tableDataSelections.forEach(item => {
          index = that.tableData.indexOf(item); //得到右侧选中值的标识坐标
          if (direction == "up") that.moveUp(index);
          else if (direction == "down") that.moveDown(index);
        });
      },
      moveUp(index) {
        //上移
        if (index == 0) return;
        this.tableData = swapItems(this.tableData, index, index - 1, "up");
      },
      moveDown(index) {
        //下移
        if (index == this.tableData.length - 1) return;
        this.tableData = swapItems(this.tableData, index, index + 1, "down");
      },
      describeClick(){
        var sortArr = [];
        if (this.tableData.length < 0) return;
        this.tableData.forEach((item, index) => {
          sortArr.push(
            item.expressionPrefix,
            item.name,
            item.operatorShow,
            item.valueShow,
            item.expressionSuffix,
            item.connectorShow,
          );
        });
        this.dataForm.sqlConditionsCh = sortArr.toString().replace(/,/g," ")
      },
      saveClick(){

        this.$refs.addForm.validate((valid) => {
          if (valid) {
            let url = ''
            if(this.operateType == 'add'){
              url = this.API.savePersonLeadercondition
            }else{
              url = this.API.updatePersonLeadercondition
            }
            var sortArr = [];
            if (this.tableData.length < 0) return;
            this.tableData.forEach((item, index) => {
              sortArr.push({
                expressionPrefix:item.expressionPrefix,
                name:item.name,
                operatorShow:item.operatorShow,
                valueShow:item.valueShow,
                expressionSuffix:item.expressionSuffix,
                connectorShow:item.connectorShow,
              });
            });
            this.$http({
              url: this.$http.adornUrl(url),
              method: 'post',
              data: this.$http.adornData({
                leaderType:this.checkedData.id,
                id:this.citiaoId,
                leaderScript:JSON.stringify(sortArr),
                leaderStatus:this.dataForm.menuStatus,
                leaderDesc:this.dataForm.sqlConditionsCh
              })
            }).then((res) =>{
              var resData = res.data;
              if(resData.code == 0) {
                this.$message({
                  message: '成功',
                  type: 'success'
                });
                this.$emit("addback",true);
              } else {
                this.$message.error(resData.msg);
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },



    }
  }
</script>

<style lang="scss" scoped>
  .box{
    height: 630px;
    .el-aside {
      border-right: 1px solid #e6e6e6;
      padding-top: 10px;
    }
    .el-table{
      height: 410px;
      overflow: auto;
    }
    .top-btn{
      float: right;
      padding: 0 0 10px 0;
    }
  }
  .el-main {
    padding: 0;
  }
</style>



